.bookDisplay__imgContainer{
    background-color:lightgray;
    width: 10rem;
    height: 11rem;
    border-radius: 20px;
    padding: 20px;
    display: flex;
    align-content: center;
    justify-content: center;
}
.bookDisplay__img{
    object-fit: contain;
    width: 100%;
    
    
}
.bookDisplay{
    display: flex;
    flex-direction: column;
    width: 10rem;
    height: 17rem;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    flex-shrink: 0;
    margin: 2rem;
   
}
.bookDisplay__subtitle{
    inline-size: 150px;
    overflow-wrap: break-word;
}
.bookDisplay__price{
    color: #0470bb;
    font-weight: 700;
    margin: 2px;
}
.bookDisplay__originalPrice{
    color: grey;
    font-weight: 600;
    text-decoration: line-through;
}
.bookDisplay__discount{
    color: limegreen;
    font-weight: 700;
    margin-top: -1rem;
}
.bookDisplay__flex{
    display: flex;
    justify-content: space-between;
    overflow-x: scroll;
}