.subbutton{
    display: flex;
    flex-direction:row;
    justify-content: space-between;
    margin-top: 2%;
}
.subbutton__classSort{
    display: flex;
}
.subbutton__examSort{
    display: flex;
}
.subbutton__btn{
    width: 5rem;
    height: 5rem;
    background-color: lightskyblue;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: .5rem;
}
.subbutton__btn > p{
    color: #0470bb;
    font-weight: 600;
    text-align: center;
    font-family: "Roboto","Helvetica","Arial",sans-serif;
    font-weight: 800;
}
.subbutton__btnWrapper{
    text-align: center;
}
.subbutton__btnWrapper > p{
    overflow-x: hidden;
    font-family: "Roboto","Helvetica","Arial",sans-serif;
    font-weight: 700;
}
a{
    text-decoration: none;
}
.subbtn__resize{
    font-size: 12px;
}

@media screen and (max-device-width:650px) {
        .subbutton{
            margin-left: 4.5rem;
            margin-right: 4.5rem;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        .subbtn__resize{
            font-size: 12px;
        }
}
@media screen and (max-width:1020px) {
    .subbutton{
        margin-left: 4.5rem;
        margin-right: 4.5rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .subbtn__resize{
        font-size: 12px;
    }
}